
@import 'variables';

.stars {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;

  &__icon {
    width: 16px;
    height: 16px;
    margin: 0 1px;
    display: block;
    filter: none;
    opacity: 1;
    transition: filter 0.3s, opacity 0.3s;
  }

  @for $i from 1 through 5 {
    &--#{$i} {
      .stars__icon:nth-child(-n+#{$i}) {
        filter: none;
        opacity: 1;
      }


      .stars__icon:nth-child(n+#{$i + 1}) {

        filter: grayscale(100%);
        opacity: 0.3;
      }
    }
  }
}
