.stars {
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  display: flex;
}

.stars__icon {
  filter: none;
  opacity: 1;
  width: 16px;
  height: 16px;
  margin: 0 1px;
  transition: filter .3s, opacity .3s;
  display: block;
}

.stars--1 .stars__icon:nth-child(-n+1) {
  filter: none;
  opacity: 1;
}

.stars--1 .stars__icon:nth-child(n+2) {
  filter: grayscale();
  opacity: .3;
}

.stars--2 .stars__icon:nth-child(-n+2) {
  filter: none;
  opacity: 1;
}

.stars--2 .stars__icon:nth-child(n+3) {
  filter: grayscale();
  opacity: .3;
}

.stars--3 .stars__icon:nth-child(-n+3) {
  filter: none;
  opacity: 1;
}

.stars--3 .stars__icon:nth-child(n+4) {
  filter: grayscale();
  opacity: .3;
}

.stars--4 .stars__icon:nth-child(-n+4) {
  filter: none;
  opacity: 1;
}

.stars--4 .stars__icon:nth-child(n+5) {
  filter: grayscale();
  opacity: .3;
}

.stars--5 .stars__icon:nth-child(-n+5) {
  filter: none;
  opacity: 1;
}

.stars--5 .stars__icon:nth-child(n+6) {
  filter: grayscale();
  opacity: .3;
}

.product-card {
  text-align: center;
  background-color: #fff;
  border: 1px solid #eee;
  border-radius: 8px;
  width: 200px;
  padding-bottom: 20px;
  transition: box-shadow .3s, border-color .3s;
}

.product-card__image-wrapper {
  padding: 10px;
}

.product-card__image {
  max-width: 100%;
  height: auto;
  margin: 0 auto 10px;
  display: block;
}

.product-card__content {
  padding: 0 15px;
}

.product-card__title {
  color: #333;
  min-height: 40px;
  margin: 0 0 5px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.4;
}

.product-card__code {
  color: #888;
  margin-bottom: 10px;
  font-size: 11px;
}

.product-card__reviews-count {
  color: #555;
  margin-left: 5px;
  font-size: 11px;
}

.product-card__price-group {
  justify-content: center;
  align-items: baseline;
  margin-bottom: 20px;
  display: flex;
}

.product-card__label {
  color: #888;
  margin-right: 5px;
  font-size: 12px;
}

.product-card__price {
  color: #000;
  font-size: 20px;
  font-weight: 700;
}

.product-card__buy-link {
  cursor: pointer;
  color: #fff;
  background-color: #0093e9;
  border: 2px solid #0093e9;
  border-radius: 6px;
  width: 90%;
  padding: 10px 0;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  transition: background-color .3s, color .3s, border-color .3s, opacity .3s;
  display: inline-block;
}

.product-card__buy-link:hover {
  opacity: .9;
}

.product-card:hover {
  border-color: #b3e0ff;
  box-shadow: 0 4px 12px #00000026;
}

.product-card:hover .product-card__buy-link {
  color: #0093e9;
  opacity: 1;
  background-color: #fff;
  border-color: #0093e9;
}

body {
  color: #333;
  background-color: #f7f7f7;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  text-align: center;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
  padding: 20px;
}

h1 {
  margin: 0;
  font-size: 24px;
}

.product-catalog {
  justify-content: center;
  gap: 30px;
  padding: 40px 20px;
  display: flex;
}
