@import './variables';
@import './stars';
@import './card';

body {
  font-family: Arial, sans-serif;
  background-color: #f7f7f7;
  color: #333;
  margin: 0;
  padding: 0;
}

header {
  text-align: center;
  padding: 20px;
  background-color: white;
  border-bottom: 1px solid #ddd;
}

h1 {
  margin: 0;
  font-size: 24px;
}

.product-catalog {
  display: flex;
  justify-content: center;
  gap: 30px;
  padding: 40px 20px;
}
